html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

#root {
  --dun: #e8d2b1;
  --platinum: #ebebe8;
  --sandy-brown: #f1a771;
  --flame: #e96838;
  --cambridge-blue: #6ea69d;
  --indigo-dye: #0b3d64;
  height: 100%;
  width: 100%;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  transition: left .2s ease-in-out;
  display: flex;
  position: relative;
  left: 0;
}

#background {
  position: inherit;
}

.sidebar {
  height: 100%;
  width: 200px;
  background-color: var(--indigo-dye);
  color: var(--sandy-brown);
  z-index: 100;
  border-top-right-radius: .5rem;
  flex-direction: column;
  align-items: center;
  margin-right: 1em;
  transition: left .2s ease-in-out;
  display: flex;
  position: absolute;
  left: -200px;
  overflow-x: scroll;
}

.pie-holder {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pie-hole {
  border: var(--sandy-brown);
  width: 41%;
  height: 70%;
  cursor: default;
  border-style: solid;
  border-radius: 100%;
  justify-content: end;
  align-items: center;
  display: flex;
}

h1 {
  letter-spacing: 1rem;
  width: 80%;
  text-align: center;
  font-family: sans-serif;
  font-size: 2.5rem;
}

#root.open {
  left: 200px;
}

.sidebar.open {
  transition: left .2s ease-in-out;
  left: 0;
}

.sidebar.close {
  overflow: visible;
}

.sidebar-toggle {
  height: 15px;
  width: 20px;
  z-index: 1;
  cursor: pointer;
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
  margin: 0 .2rem;
  display: flex;
  position: absolute;
  right: 0;
}

.sidebar-toggle.close {
  color: var(--indigo-dye);
  margin: 0;
  right: -40px;
}

.sidebar-component {
  width: 80%;
  border-style: solid;
  border-color: var(--sandy-brown);
  opacity: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: .5rem;
  padding: 5px 10px;
  display: flex;
  position: relative;
}

.component-button {
  width: 80%;
  letter-spacing: .05rem;
  border: var(--sandy-brown);
  text-align: center;
  cursor: pointer;
  border-style: solid;
  border-radius: .5rem;
  justify-content: center;
  align-items: center;
  margin: 1px 0;
  padding: .15rem .25rem;
  display: flex;
}

.component-button:active {
  background-color: var(--cambridge-blue);
  color: var(--dun);
}

.sortable {
  border: none;
  border-bottom: var(--dun);
  height: .75rem;
  cursor: default;
  border-bottom-style: solid;
  border-bottom-width: .5px;
  flex-direction: row;
  justify-content: space-between;
  margin: 0;
  display: flex;
}

.disabled {
  cursor: default;
  opacity: .6;
}

.sortable:hover {
  background-color: var(--cambridge-blue);
  color: var(--dun);
}

.disabled:hover {
  background-color: var(--indigo-dye);
}

.selectable {
  border: none;
  border-bottom: var(--dun);
  height: .75rem;
  cursor: default;
  border-bottom-style: solid;
  border-bottom-width: .5px;
  flex-direction: row;
  justify-content: space-between;
  margin: 0;
  display: flex;
}

.reset_button {
  letter-spacing: 2px;
  cursor: default;
  text-align: center;
  border-radius: .15rem;
  justify-content: center;
  align-self: flex-end;
  align-items: center;
  margin: -3px -5px 2px 0;
  padding: .05rem .2rem;
  font-size: .4rem;
  display: flex;
}

.reset_button:hover {
  background: var(--cambridge-blue);
  color: var(--dun);
}

.reset_button:active {
  background-color: var(--flame);
  color: var(--dun);
}

.label-containter {
  width: 100%;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  justify-content: space-between;
  align-items: end;
  margin-bottom: 1px;
  font-size: .7rem;
  display: flex;
}

